interface SingleEntityLogParams {
  action: string;
  entitySingular: string;        
  count: never;     
  [key: string]: any;
}

interface MultipleEntityLogParams {
  action: string;
  entityPlural: string;        
  count: number;         
  [key: string]: any;
}

export type LogMessageParams = SingleEntityLogParams | MultipleEntityLogParams;

export const getLogMessageKeys = (
  action: string,
  entity: string,
  count?: number
): { translationKey: string; params: LogMessageParams } => {
  if (typeof count === "number" && count > 1) {
    // Plural form
    return {
      translationKey: "log.messages.multiple",
      params: {
        action,
        entityPlural: entity,
        count,
      } satisfies MultipleEntityLogParams,
    };
  } else {
    // Singular form
    return {
      translationKey: "log.messages.single",
      params: {
        action,
        entitySingular: entity,
        count: undefined as never, 
      } satisfies SingleEntityLogParams,
    };
  }
};